import axios from 'axios'

var url = ''

class ApiService {
    // insert User with user
    static newClient(clientUrl) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(`${url}/new-client`, {
                    clientUrl: clientUrl
                })
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err)
            }
        })
    } // end insertUser
} // end class UserService

export default ApiService