<template>
    <div class="view">
        <div class="wrapper">
            <div class="container center">
                <div class="holder">
                    <h2>Welcome to <span>SYNCRAWLER</span></h2>
                    <h3>Generate search engine</h3>
                    <p>Enter your site's base URL</p>
                    <input autofocus v-on:keyup.enter="submit" class="input" v-model="input" type="text" placeholder="https://www.example.com/">
                    <button class="button" v-on:click="submit">SUBMIT</button>
                </div>
            </div>
        </div>
        <Popup v-if="popup" v-bind:scriptTag="scriptTag" v-on:close="close"></Popup>
    </div>
</template>

<script>
import Popup from './Popup'
import ApiService from '../ApiService'

export default {
    name: 'WelcomePage',
    components: {
        Popup
    },
    data: function() {
        return {
            popup: false,
            scriptTag: '',
            input: ''
        }
    },
    methods: {
        submit: async function() {
            this.scriptTag = await ApiService.newClient(this.input)
            this.popup = true;
        },
        close: function() {
            this.input = ''
            this.popup = false
        }
    }
}
</script>

<style scoped>
.view {
    position: fixed;
    z-index: 9000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: table;
}

.wrapper {
    display: table-cell;
}

.container {
    height: 20rem;
    width: 30rem;
    margin: auto;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    border-radius: 1.5rem;
}

.holder {
    padding: 2rem;
    text-align: center;
}

h2 {
    margin: 0;
}
h2 span {
    color:#f7e930;
    font-weight:1000;
}

h3 {
    margin-top: .2rem;
}

.input {
    width: 20rem;
    border-radius: .5rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    height: 1.5rem;
    border: solid 1px black;
    padding: .5rem;
    transition: border-color 500ms ease;
}

.input:focus {
    /*border-color: linear-gradient(90deg, #f15a24, #fffd84);*/
    border-color: #2043db;
    outline: none;
}

.center {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);  
}

.button {
    margin: 0 10rem;
    color: white;
    background-color: #2043db;
    border-radius: .5rem;
    border: solid;
    outline: none;
    transition: background-color 500ms ease, color 500ms ease;
}

.button:hover {
    background-color: white;
    color: #2043db;
}
</style>